___CSS_LOADER_EXPORT___.push([module.id, `@media screen and (max-width:1200px) {
    body, html {
        position: relative;
        overflow-x: hidden;
    }
    .navbar{
        width: 100%;
    }
    .logo{
        margin-left: 30px;
    }
    .nav_list{
        margin-left: -20%;
    }
    .hero{
        width: 100%;
    }

    .hero h1{
        width: fit-content;
    }
    .hero p{
        width: fit-content;
    }
    main {
        width: 100%;
        margin: 0 auto;
    }

    .card {
        margin: 10px auto 10px auto;
        width: 30%;
        height: auto;
        box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.2);
        border-radius: 20px;
        background-color: white;
    }

}

/* Ipad Air */
@media screen and (max-width: 820px) {
    body, html {
        position: relative;
        overflow-x: hidden;
    }
    .nav_list li a {
        color: white;
        font-size: 20px;
    }
    .nav_list {
        width: 30%;
        height: 35vh;
        position: absolute;
        right: 0px;
        top: 92px;
        line-height: 60px;
        align-items: center;
        padding: 20px;
        border-radius: 0 0 0 20px;
        background-color: #405F8D;
        display: inline-block;
        flex-direction: column;
        transform: translateX(100%);
        transition: transform 0.5s ease-in;
    }

    
    .hamburger{
        display: block;
        margin-left: -53%;        
    }
    .thumbnail {
        width: 100%;
        height: auto;
    }
    .nav-active{
        transform: translateX(0%);
    }

}

/* Ipad Mini (768) */
@media screen and (max-width: 770px) {
    main {
        width: 100%;
        margin: 0 auto
    
    }
    .card {
        margin: 10px auto 10px auto;
        width: 48%;
        height: auto;
        box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.2);
        border-radius: 20px;
        background-color: white;
    }
    .thumbnail {
        width: 100%;
        height: auto;
    }
    .nav-active{
        transform: translateX(0%);
    }
}


@media screen and (max-width:500px) {
    body, html {
        position: relative;
        overflow-x: hidden;
    }
    .nav_list li a {
        color: white;
        font-size: 20px;
    }
    .nav_list {
        width: 50%;
        height: 40vh;
        position: absolute;
        right: 0px;
        top: 92px;
        line-height: 50px;
        align-items: center;
        padding: 20px;
        border-radius: 0 0 0 20px;
        background-color: #405F8D;
        display: flex;
        flex-direction: column;
        transition: transform 0.5s ease-in;

    }
    
    .hamburger{
        display: block;
        margin-left: -90%;
    }
    .hero{
        width: 100%;
        padding-left: 50px;
    }
    .hero p{
        font-size: 26px;
    }
    .hero h1{
        font-size: 30px;
    }
    .card {
        margin: 10px auto 10px auto;
        width: 67%;
        height: auto;
        box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.2);
        border-radius: 20px;
        background-color: white;
    }
}

/* Samsung Galaxy A51/A71 */
@media screen and (max-width:412px) {
    body, html {
        position: relative;
        overflow-x: hidden;
    }
    .hero{
        width: 100%;
        padding-left: 20px;
    }
    .hero p{
        font-size: 26px;
    }
    .hero h1{
        font-size: 28px;
    }
    .hamburger{
        display: block;
        margin-left: -95%;
    }
    .card {
        margin: 10px auto 10px auto;
        width: 80%;
        height: auto;
        box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.2);
        border-radius: 20px;
        background-color: white;
    }

    
}

/* Iphone 12 Pro */
@media screen and (max-width:390px){
    .hero{
        width: 100%;
        padding-left: 20px;
    }
    .hero p{
        font-size: 24px;
    }
    .hero h1{
        font-size: 26px;
    }
    .hamburger{
        display: block;
    }
}

